
.navBox {
	height:130px;
	background-color: #fff;
	background-color: #fff;
}
.navBox p {
	width:186px;
	margin:0 auto;
	text-align: center;
	border-bottom: 1px solid;
	height:35px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack:center;
	    -ms-flex-pack:center;
	        justify-content:center;
}
.navBox p img {
	width:25px;
	height:25px;
	margin-top: 5px;
}
.navBox p span {
	font-size: 12px;
	line-height: 35px;
}
.navList {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	height:90px;
}
.navList a {
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	text-align: center;
	line-height: 158px;
	font-size: 16px;
	color:#7b7b7b;
}
.navList a:nth-child(1) {
	background: url(../../../static/images/newUser.png) no-repeat;
	background-position: center 5px;
}
.navList a:nth-child(2) {
	background: url(../../../static/images/myRun.png) no-repeat;
	background-position: center 5px;
}
.navList a:nth-child(3) {
	background: url(../../../static/images/myTravel.png) no-repeat;
	background-position: center 5px;
}
.navList a:nth-child(4) {
	background: url(../../../static/images/issue.png) no-repeat;
	background-position: center 5px;
}



